.card {
    width: 100%;
    height: 250px;
    overflow: hidden !important;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.card .content {
    font-family: "Open Sans";
    text-align: center;
    padding: 10px;
    opacity: 0;
    transition: all 500ms ease-in-out;
    transform: translate(0, 20px);
    cursor: pointer;
}

.card .content h1 {
    font-size: 14px;
    color: white;
}

.card .content p {
    font-size: 14px;
    color: white;
    margin-bottom: 20px;
}

.card .content .btn {
    background-color: black;
    color: white;
    padding: 12px;
    text-decoration: none;
}

.card .content:hover {
    opacity: 1;
    transform: translate(0, 0px);
}