.about-section {
    width: 80vw;
    margin-top: 10vh;
    display: flex;
    justify-content: space-between;
}

.about-section .content {
    width: 50%;
}

.about-section .content h1 {
    font-size: 3rem;
    line-height: 20px;
}

.about-section .image-wrapper {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}