.navigation {
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 5px 0;
    background-color: #172b4d;
    color: white !important;
}

.nav-name {
    text-decoration: none;
    color: white;
    font-size: 1.3rem;
    font-weight: bold;
    margin-left: 1rem;
}

.navigation-menu {
    margin-left: auto;
}

.navigation-menu ul {
    display: flex;
    padding: 0;
}

.navigation-menu li {
    list-style-type: none;
    margin: 0 1rem;
}

.navigation-menu li a {
    color: white;
    text-decoration: none;
    display: block;
    width: 100%;
}

.hamburger {
    border: 0;
    height: 40px;
    width: 40px;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: white;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;

    position: absolute;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    display: none;
}

.hamburger:hover {
    background-color: grey;
}

.hamburger svg {
    fill: #172b4d;
}

@media screen and (max-width: 768px) {
    .hamburger {
        display: block;
    }

    .navigation-menu.expanded ul {
        display: block;
    }

    .navigation-menu ul {
        display: none;

        position: absolute;
        top: 40px;
        left: 0;

        flex-direction: column;

        width: 100%;
        height: calc(100vh - 77px);
        background-color: white;
        border-top: 1px solid black;
    }

    .navigation-menu li {
        text-align: center;
        margin: 0;
    }

    .navigation-menu li a {
        width: 100%;
        padding: 1.5rem 0;
    }

    .navigation-menu li:hover {
        background-color: blue;
    }
}